
import moment from 'moment';
const month_to_vi = {
    1: 'thng 1',
    2: 'thng 2',
    3: 'thng 3',
    4: 'thng 4',
    5: 'thng 5',
    6: 'thng 6',
    7: 'thng 7',
    8: 'thng 8',
    9: 'thng 9',
    10: 'thng 10',
    11: 'thng 11',
    12: 'thng 12'
}
export default class DateTimeUtil {
    static diffTime(time) {
        // Second
        const secondDiff = Math.ceil(Math.abs(new Date().getTime() - time * 1000) / 1000);
        if (secondDiff < 60) {
            return secondDiff + ' giy trc';
        }

        const minuteDiff = Math.ceil(secondDiff / 60);
        if (minuteDiff < 60) {
            return minuteDiff + ' pht trc';
        }

        const hourDiff = Math.ceil(minuteDiff / 60);
        if (hourDiff < 24) {
            return hourDiff + ' gi trc';
        }

        const dayDiff = Math.ceil(hourDiff / 24);

        if (dayDiff < 3) {
            return `${dayDiff} ngy trc`;
        }

        return moment.unix(time).format('DD/MM/YYYY');
    }



    static getArrayMonth() {
        return [
            { name: 'Thng 1', id: 1 },
            { name: 'Thng 2', id: 2 },
            { name: 'Thng 3', id: 3 },
            { name: 'Thng 4', id: 4 },
            { name: 'Thng 5', id: 5 },
            { name: 'Thng 6', id: 6 },
            { name: 'Thng 7', id: 7 },
            { name: 'Thng 8', id: 8 },
            { name: 'Thng 9', id: 9 },
            { name: 'Thng 10', id: 10 },
            { name: 'Thng 11', id: 11 },
            { name: 'Thng 12', id: 12 }
        ]
    }

    static getArrayYear() {
        let array = [];
        for (var i = 1920; i <= 2020; i++) {
            array.push(i);
        }
        return array;
    }

    static getArrayDay(month, year) {
        let maxDay = new Date(year, month, 0).getDate();
        let array = [];
        for (var i = 1; i <= maxDay; i++) {
            array.push(i);
        }
        return array;
    }

    static formatMonth(date) {
        var month = date.getMonth() + 1;
        var year = date.getFullYear();
        return "Thng " + month + " nm " + year;
    }

    static toString(timestamp) {
        let date = new Date(timestamp);
        let day = date.getDate();
        let month = date.getMonth() + 1;
        let year = date.getFullYear();
        return day + ' ' + month_to_vi[month] + ', ' + year;
    }

}

